import React from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField';
import SearchIcon from '@mui/icons-material/Search';
import Avatar from '@mui/material/Avatar';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import SettingData from '../../datasource/setting-data'
import SettingItem from './SettingItem'
import {useNavigate} from 'react-router-dom'
import Enumerable from "linq-js";




/**
 * 
 */
export default function SettingHome(props) {
	console.clear()

	let navigate = useNavigate()

	const displayItemList = []
	SettingData.asEnumerable().forEach((one, i) => {
		displayItemList.push(<SettingItem data={one} padding={"0.1em 0"} key={`app-item-${i}`} />)
	})


	return (
		<Box sx={{padding: '1em'}}>

			<h1 style={{borderBottom: 'solid 1px'}}>
				
			</h1>


			{/**/}
			<Box sx={{display: 'flex', alignItems: 'flex-end'}}>
				<SearchIcon sx={{color: 'action.active', mr: 1, my: 0.5}} />
				<TextField id="input-with-sx" label="" variant="standard" sx={{width: '100%'}} />
			</Box>


			{/**/}
			<List>
				<ListItemButton>
					{/**/}
					<Avatar sx={{
						width: '3em',
						height: '3em',
						marginLeft: '-0.5em'
					}} />
					<div style={{margin: '1em'}}>
						<h3 style={{margin: 0}}></h3>
						<label></label>
					</div>
				</ListItemButton>


				{
					displayItemList
				}

			</List>

		</Box>
	)
}